.contacts-section {
  background-image: linear-gradient(#ffffff 0% 6%, #fff5f6 6% 100%);

  @media screen and (min-width: 480px) {
    background-image: linear-gradient(#ffffff 0% 6%, #fff5f6 6% 100%);
  }

  @media screen and (min-width: 768px) {
    background-image: linear-gradient(#ffffff 0% 8%, #fff5f6 8% 100%);
  }

  @media screen and (min-width: 1280px) {
    background-image: linear-gradient(#ffffff 0% 12%, #fff5f6 12% 100%);
  }
}

.contacts__container {
  padding-bottom: 113px;

  @media screen and (min-width: 480px) {
    padding-bottom: 113px;
  }

  @media screen and (min-width: 768px) {
    padding-bottom: 165px;
  }

  @media screen and (min-width: 1280px) {
    padding-bottom: 165px;
  }
}

.contacts__list {
  margin: 0 auto;
  max-width: 220px;
  margin-bottom: 54px;

  @media screen and (min-width: 480px) {
    max-width: 220px;
    margin-bottom: 54px;
  }

  @media screen and (min-width: 768px) {
    display: flex;
    max-width: 700px;
    margin-right: 2px;
    margin-left: 2px;
    margin-bottom: 81px;
  }

  @media screen and (min-width: 1280px) {
    max-width: 1050px;
    margin-right: 0px;
    margin-left: 0px;
    margin-bottom: 86px;
  }
}

.contacts__card {
  padding: 43px 21px 43px 20px;
  background: var(--white-background-color);
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;

  &:not(:last-child) {
    margin-bottom: 20px;
  }

  @media screen and (min-width: 480px) {
  }

  @media screen and (min-width: 768px) {
    padding: 50px 32px 50px 31px;
    &:not(:last-child) {
      margin-right: 20px;
      margin-bottom: 0px;
    }
  }

  @media screen and (min-width: 1280px) {
    &:not(:last-child) {
      margin-right: 31px;
    }
  }
}
.contacts__card-wrap {
  margin: 0 auto;

  @media screen and (min-width: 480px) {
  }

  @media screen and (min-width: 768px) {
  }

  @media screen and (min-width: 1280px) {
  }
}

.contacts_title-board {
  border-radius: 4px;

  @media screen and (min-width: 480px) {
    border-radius: 4px;
  }

  @media screen and (min-width: 768px) {
    border-radius: 6px;
  }

  @media screen and (min-width: 1280px) {
    border-radius: 6px;
  }
}

.contacts_title-board-brown {
  background-color: var(--brown-background-color);
  width: 80px;

  @media screen and (min-width: 480px) {
    border-radius: 80px;
  }

  @media screen and (min-width: 768px) {
    border-radius: 73px;
  }

  @media screen and (min-width: 1280px) {
    border-radius: 73px;
  }
}

.contacts_title-board-green {
  background-color: var(--green-background-color);
  width: 130px;

  @media screen and (min-width: 480px) {
    border-radius: 130px;
  }

  @media screen and (min-width: 768px) {
    border-radius: 138px;
  }

  @media screen and (min-width: 1280px) {
    border-radius: 138px;
  }
}

.contacts__category-title {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.86;
  text-align: center;

  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: var(--white-text-color);
}
.contacts__card-item {
  padding-top: 25px;
  padding-bottom: 25px;
  border-bottom: 1px solid var(--separating-line-color);

  @media screen and (min-width: 480px) {
  }

  @media screen and (min-width: 768px) {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  @media screen and (min-width: 1280px) {
    padding-top: 32px;
    padding-bottom: 32px;
  }
}
.contacts__title {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.86;
  letter-spacing: 0.04em;

  color: var(--dark-text-color);

  @media screen and (min-width: 480px) {
    font-size: 12px;
    line-height: 1.86;
  }

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.86;
  }

  @media screen and (min-width: 1280px) {
    font-size: 16px;
    line-height: 1.86;
  }
}

.contacts_city {
  margin-bottom: 10px;

  @media screen and (min-width: 480px) {
    margin-bottom: 10px;
  }

  @media screen and (min-width: 768px) {
    margin-bottom: 14px;
  }

  @media screen and (min-width: 1280px) {
    margin-bottom: 10px;
  }
}

.contacts__title-text {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.5;
  color: var(--light-text-color);

  @media screen and (min-width: 480px) {
    font-size: 12px;
    line-height: 1.5;
  }

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.66;
  }

  @media screen and (min-width: 1280px) {
    font-size: 16px;
    line-height: 1.83;
  }
}
