.contacts__tel {
  letter-spacing: 0;
  transition: color 250ms linear;

  &:hover,
  &:focus {
    color: var(--brand-color);
  }
}

.contacts__mail {
  letter-spacing: 0;
  transition: color 250ms linear;

  &:hover,
  &:focus {
    color: var(--brand-color);
  }
}

.contacts__btn {
  margin: 0 auto;
  width: 161px;
  align-items: center;

  @media screen and (min-width: 480px) {
  }

  @media screen and (min-width: 768px) {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  @media screen and (min-width: 1280px) {
  }
}

.contacts__btn-item {
  &:not(:last-child) {
    margin-bottom: 10px;
  }

  @media screen and (min-width: 480px) {
  }

  @media screen and (min-width: 768px) {
    &:not(:last-child) {
      margin-bottom: 0px;
      margin-right: 14px;
    }
  }

  @media screen and (min-width: 1280px) {
    &:not(:last-child) {
      margin-right: 15px;
    }
  }
}

.contacts__btn-location {
  width: 161px;

  @media screen and (min-width: 480px) {
    width: 161px;
  }

  @media screen and (min-width: 768px) {
    width: 191px;
  }

  @media screen and (min-width: 1280px) {
    width: 198px;
  }
}

.contacts__btn-franchise {
  width: 161px;

  @media screen and (min-width: 480px) {
    width: 161px;
  }

  @media screen and (min-width: 768px) {
